<template>
  <div id="detail">
    <h1>クリア状況</h1>

    <label>
      <select class="form-control" name="level" v-model="selected_level">
        <option v-for="(level,key) in tables[table_index()].levels" :key="key">{{ level }}</option>
      </select>
    </label>

    <br/>
    <table class="table table-bordered">
      <thead>
      <tr>
        <td>Lv</td>
        <th>Title</th>
        <td>Rate</td>
        <td>EX/MAX</td>
        <td>BP</td>
        <td>Combo</td>
        <td>Play</td>
        <td>Date</td>
      </tr>
      </thead>
      <SongDetail
          v-for="song in songs[table_index()][level_index()].songs"
          :key="song.title"
          :level="selected_level"
          :title="song.title"
          :score="song.score"
          :min_bp="song.min_bp"
          :max_combo="song.max_combo"
          :clear_type="song.clear_type"
          :play_count="song.play_count"
          :updated_at="song.updated_at"
          :total_notes="song.total_notes"
      />
    </table>
  </div>
</template>

<script>
import SongDetail from "./SongDetail";

const song_format = [
  [
    {
      level: "",
      songs: [
        {
          title: "",
          score: "",
          min_bp: "",
          max_combo: "",
          clear_type: "",
          updated_at: "",
          play_count: "",
          total_notes: "",
        }]
    }
  ]
];

export default {
  name: "Detail",
  components: {SongDetail},
  props: {
    tables: {
      type: Array,
      required: true
    },
    selected_table: {
      type: String,
      required: true
    }
  },
  data: () => ({
    songs: [song_format],
    selected_level: ""
  }),
  methods: {
    fetch_detail() {
      fetch(process.env.VUE_APP_HOST + "detail/").then(response => {
        return response.json()
      })
          .then(json => {
            console.log(json);
            for (let i = 0; i < this.tables.length; i++) {
              this.songs.splice(i, 1, json[i].Detail.levels);
            }
          })
          .catch((err) => {
            this.msg = err
          });
    },
    table_index() {
      for (let i = 0; i < this.tables.length; i++) {
        if (this.tables[i].name === this.selected_table) {
          return i;
        }
      }
      console.error("難易度表が読み込まれてなさそうです");
      return 0;
    },
    level_index() {
      for (let i = 0; i < this.tables[this.table_index()].levels.length; i++) {
        if (this.tables[this.table_index()].levels[i] === this.selected_level) {
          return i;
        }
      }
      this.selected_level = this.tables[this.table_index()].levels[0];
      return 0;
    }
  },
  created: function () {
    this.selected_level = this.tables[0].levels[0];

    const songs = Array(this.tables.length);
    songs.fill(song_format);
    this.songs = songs;
    this.fetch_detail();
  }
}
</script>

<style scoped></style>